/**
 * Annotation for marking a page input property
 * @param builder so you can tweak stuff
 * @returns {(target:any, name:string) => void} field decorator
 */
export function InputProperty(... decorators: any[]): (target: any, name: string) => void {
   let data = new InputPropertyData();
   if (decorators) {
      decorators.forEach(d => {
         if (typeof d == "string") {
            d = InputProperty.named(d);
         }
         d(data)
      });
   }
   return (target: any, name: string) => {
      let className = target.constructor.name;

      data.selector = name;
      if (!data.name) {
         data.name = name;
      }
      PageData.get(className).input.push(data);
   };
}

export module InputProperty {
   export function named(name: string) {
      return function(builder: InputPropertyBuilder) {
         builder.named(name);
      }
   }
   export function required(value: boolean) {
      return function(builder: InputPropertyBuilder){
         builder.required(value);
      }
   }
}

/**
 * Annotation for marking a page output property
 * @param builder for convenient neatness
 */
export function OutputProperty(... decorators: any[]): (target: any, name: string) => void {
   let data = new OutputPropertyData();
   if (decorators) {
      decorators.forEach(d => {
         if (typeof d == "string") {
            d = InputProperty.named(d);
         }
         d(data)
      });
   }
   return (target: any, name: string) => {
      let className = target.constructor.name;

      data.selector = name;

      if (typeof data.name === 'undefined') {
         data.name = name;
      }
      PageData.get(className).output.push(data);
   }
}

export module OutputProperty {
   export function named(name: string) {
      return function(builder: OutputPropertyBuilder) {
         builder.named(name);
      }
   }
   export function validatorProperty(property: string) {
      return function(builder: OutputPropertyBuilder) {
         builder.validateRemote(property);
      }
   }
   export function ifNull(nullMessage: string) {
      return function(builder: OutputPropertyBuilder) {
         builder.validateNotNull(nullMessage);
      }
   }
   export function ifEmpty(emptyMessage: string) {
      return function(builder: OutputPropertyBuilder) {
         builder.validateNotEmpty(emptyMessage);
      }
   }
}

/**
 * Annotation for marking a component as page.
 * @param title
 * @param description - string or function (all page inputs may be used as incoming parameters)
 * @param services specific list of services for the page
 * @returns {(pageClass:any)=>undefined} type decorator
 */
export function WizardPage(
      title: string,
      description?: string | Function,
      services?: any[]): (type: any) => void {
   return (pageClass: any) => {
      let data: PageData = PageData.get(pageClass.name);
      data.title = title;
      data.pageTypeName = pageClass.name;

      if (description) {
         if (typeof description === "function") {
            data.description = <any> description;
         } else {
            data.description = () => ("" + description);
         }
      } else {
         data.description = null;
      }

      if (services) {
         data.services = services || [];
      }
   }
}

/**
 * Accessible in the @InputProperty annotation to
 * customize the property data.
 */
export interface InputPropertyBuilder {

   /**
    * Override the name of the property.
    * Default is accessor name.
    */
   named(name: string): InputPropertyBuilder;

   /**
    * Overrides whether the input property is required.
    * Default is true.
    */
   required(value: boolean): InputPropertyBuilder;

}


/**
 * Accessible in the @InputProperty annotation to
 * customize the property data.
 */
export interface OutputPropertyBuilder {

   /**
    * Override the name of the property.
    * Default is accessor name.
    */
   named(name: string): OutputPropertyBuilder;

   /**
    * Sets the local validator callback for the property to
    * not-null checker returning the specified message as error.
    */
   validateNotNull(message: string): OutputPropertyBuilder;

   /**
    * Sets the local validator callback for the property to
    * not-null-or-empty checker returning the specified message as error.
    * @param message
    */
   validateNotEmpty(message: string): OutputPropertyBuilder;

   /**
    * Assigns a remote property name to be collected during the page
    * remote validation phase. A non-null result is interpreted as error.
    */
   validateRemote(propertyName: string): OutputPropertyBuilder;

}

/**
 * Holds the annotation data for an input property.
 */
export class InputPropertyData implements InputPropertyBuilder {

   public selector: string;
   public name: string;
   public isRequired: boolean = true;

   constructor() {}

   public named(name: string) {
      this.name = name;
      return this;
   }

   public required(value: boolean): InputPropertyData {
      this.isRequired = value;
      return this;
   }

   public toString(): string {
      return this.name;
   }

}

/**
 * Holds the annotation data of an output property.
 */
export class OutputPropertyData implements OutputPropertyBuilder {

   public selector: string;
   public name: string;
   public validator: (value: any) => string[] = (anyVal) => null;
   public remoteValidatorProperty: string;

   constructor() {}

   public named(name: string) {
      this.name = name;
      return this;
   }

   public validate(validator: (value: any) => string[]) {
      this.validator = validator;
      return this;
   }

   public validateNotNull(message: string) {
      return this.validate(value => value ? null : [message]);
   }

   public validateNotEmpty(message: string) {
      return this.validate(value => {
         if (typeof value === 'undefined' || value == null) {
            return [message];
         }
         if (value.hasOwnProperty('length') && value['length'] == 0) {
            return [message];
         }
         return null;
      });
   }

   public validateRemote(propertyName: string) {
      this.remoteValidatorProperty = propertyName;
      return this;
   }

   public toString(): string {
      return this.name;
   }

}

/**
 * Holds the annotation data of a page, like
 * title, description, input and output properties.
 */
export class PageData {

   private static content = {};

   public static get(pageClass: string): PageData {
      if (!PageData.content.hasOwnProperty(pageClass)) {
         let data = new PageData();
         data.title = pageClass;
         data.description = () => pageClass;
         data.pageTypeName = pageClass;
         PageData.content[pageClass] = data;
      }
      return PageData.content[pageClass];
   }

   public title: string;
   public description: () => string;
   public input: InputPropertyData[] = [];
   public output: OutputPropertyData[] = [];
   public services: any[] = [];
   public pageTypeName: string;

   public inputByName(name: string): InputPropertyData {
      let index = this.input.findIndex((item) => item.name == name);
      return index != -1 ? this.input[index] : null;
   }

   public inputBySelector(selector: string): InputPropertyData {
      let index = this.input.findIndex((item) => item.selector == selector);
      return index != -1 ? this.input[index] : null;
   }

   public outputByName(name: string): OutputPropertyData {
      let index = this.output.findIndex((item) => item.name == name);
      return index != -1 ? this.output[index] : null;
   }

   public outputBySelector(selector: string): OutputPropertyData {
      let index = this.output.findIndex((item) => item.selector == selector);
      return index != -1 ? this.output[index] : null;
   }

}

